/**
 * @file Global Error Handler Middleware
 * @description ÂÖ®Â±ÄÂºÇÂ∏∏Â§ÑÁêÜ‰∏≠Èó¥‰ª∂ÔºåÁªü‰∏ÄÂ§ÑÁêÜÊâÄÊúâAPIË∑ØÁî±ÁöÑÂºÇÂ∏∏
 * @author ZK-Agent Team
 * @date 2024-12-19
 */

import { NextRequest, NextResponse } from 'next/server';
import { ZodError } from 'zod';
import { AgentError, ErrorSeverity, AgentErrorType } from '@/lib/errors/agent-errors';
import { ApiResponseWrapper, ApiLogger } from '@/lib/utils/api-helper';
import { ErrorCode } from '@/types/core';
import { errorMonitor } from '@/lib/monitoring/error-monitor';
import { randomUUID } from 'crypto';

// ÈîôËØØÂàÜÁ±ªÂô®
class ErrorClassifier {
  static classifyError(error: any): {
    type: AgentErrorType;
    severity: ErrorSeverity;
    statusCode: number;
    userMessage: string;
    shouldRetry: boolean;
  } {
    // ZodÈ™åËØÅÈîôËØØ
    if (error instanceof ZodError) {
      return {
        type: AgentErrorType.AUTHENTICATION_ERROR,
        severity: ErrorSeverity.LOW,
        statusCode: 400,
        userMessage: 'ËØ∑Ê±ÇÂèÇÊï∞È™åËØÅÂ§±Ë¥•',
        shouldRetry: false
      };
    }

    // Êô∫ËÉΩ‰ΩìÈîôËØØ
    if (error instanceof AgentError) {
      return {
        type: error.type,
        severity: error.severity,
        statusCode: this.getStatusCodeFromErrorType(error.type),
        userMessage: this.getUserMessageFromError(error),
        shouldRetry: this.isRetryableError(error.type)
      };
    }

    // ÁΩëÁªúÈîôËØØ
    if (error.code === 'ECONNREFUSED' || error.code === 'ENOTFOUND') {
      return {
        type: AgentErrorType.SERVICE_UNAVAILABLE,
        severity: ErrorSeverity.HIGH,
        statusCode: 503,
        userMessage: 'ÊúçÂä°ÊöÇÊó∂‰∏çÂèØÁî®ÔºåËØ∑Á®çÂêéÈáçËØï',
        shouldRetry: true
      };
    }

    // Ë∂ÖÊó∂ÈîôËØØ
    if (error.code === 'ETIMEDOUT' || error.message?.includes('timeout')) {
      return {
        type: AgentErrorType.CHAT_API_ERROR,
        severity: ErrorSeverity.MEDIUM,
        statusCode: 408,
        userMessage: 'ËØ∑Ê±ÇË∂ÖÊó∂ÔºåËØ∑ÈáçËØï',
        shouldRetry: true
      };
    }

    // ÊùÉÈôêÈîôËØØ
    if (error.message?.includes('unauthorized') || error.message?.includes('forbidden')) {
      return {
        type: AgentErrorType.AUTHENTICATION_ERROR,
        severity: ErrorSeverity.MEDIUM,
        statusCode: 401,
        userMessage: 'Ë∫´‰ªΩÈ™åËØÅÂ§±Ë¥•',
        shouldRetry: false
      };
    }

    // ÈªòËÆ§‰∏∫ÂÜÖÈÉ®ÊúçÂä°Âô®ÈîôËØØ
    return {
      type: AgentErrorType.SERVICE_UNAVAILABLE,
      severity: ErrorSeverity.CRITICAL,
      statusCode: 500,
      userMessage: 'Á≥ªÁªüÂÜÖÈÉ®ÈîôËØØÔºåÊàë‰ª¨Ê≠£Âú®Â§ÑÁêÜ',
      shouldRetry: false
    };
  }

  private static getStatusCodeFromErrorType(type: AgentErrorType): number {
    const statusMap: Record<AgentErrorType, number> = {
      [AgentErrorType.CAD_FILE_PARSE_ERROR]: 400,
      [AgentErrorType.CAD_FORMAT_UNSUPPORTED]: 415,
      [AgentErrorType.CAD_FILE_CORRUPTED]: 400,
      [AgentErrorType.CAD_ANALYSIS_TIMEOUT]: 408,
      [AgentErrorType.POSTER_GENERATION_FAILED]: 500,
      [AgentErrorType.POSTER_TEMPLATE_ERROR]: 400,
      [AgentErrorType.POSTER_RESOURCE_LIMIT]: 429,
      [AgentErrorType.POSTER_TIMEOUT]: 408,
      [AgentErrorType.CHAT_CONTEXT_LOST]: 400,
      [AgentErrorType.CHAT_API_ERROR]: 502,
      [AgentErrorType.CHAT_RATE_LIMIT]: 429,
      [AgentErrorType.CHAT_MODEL_UNAVAILABLE]: 503,
      [AgentErrorType.AGENT_COMMUNICATION_ERROR]: 502,
      [AgentErrorType.RESOURCE_EXHAUSTED]: 503,
      [AgentErrorType.SERVICE_UNAVAILABLE]: 503,
      [AgentErrorType.AUTHENTICATION_ERROR]: 401
    };
    return statusMap[type] || 500;
  }

  private static getUserMessageFromError(error: AgentError): string {
    const messageMap: Record<AgentErrorType, string> = {
      [AgentErrorType.CAD_FILE_PARSE_ERROR]: 'CADÊñá‰ª∂Ëß£ÊûêÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•Êñá‰ª∂Ê†ºÂºè',
      [AgentErrorType.CAD_FORMAT_UNSUPPORTED]: '‰∏çÊîØÊåÅÁöÑCADÊñá‰ª∂Ê†ºÂºè',
      [AgentErrorType.CAD_FILE_CORRUPTED]: 'CADÊñá‰ª∂Â∑≤ÊçüÂùèÔºåËØ∑ÈáçÊñ∞‰∏ä‰º†',
      [AgentErrorType.CAD_ANALYSIS_TIMEOUT]: 'CADÂàÜÊûêË∂ÖÊó∂ÔºåËØ∑Á®çÂêéÈáçËØï',
      [AgentErrorType.POSTER_GENERATION_FAILED]: 'Êµ∑Êä•ÁîüÊàêÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
      [AgentErrorType.POSTER_TEMPLATE_ERROR]: 'Êµ∑Êä•Ê®°ÊùøÈîôËØØ',
      [AgentErrorType.POSTER_RESOURCE_LIMIT]: 'ËµÑÊ∫ê‰ΩøÁî®Ë∂ÖÈôêÔºåËØ∑Á®çÂêéÈáçËØï',
      [AgentErrorType.POSTER_TIMEOUT]: 'Êµ∑Êä•ÁîüÊàêË∂ÖÊó∂ÔºåËØ∑ÈáçËØï',
      [AgentErrorType.CHAT_CONTEXT_LOST]: 'ÂØπËØù‰∏ä‰∏ãÊñá‰∏¢Â§±ÔºåËØ∑ÈáçÊñ∞ÂºÄÂßãÂØπËØù',
      [AgentErrorType.CHAT_API_ERROR]: 'ÂØπËØùÊúçÂä°ÊöÇÊó∂‰∏çÂèØÁî®ÔºåËØ∑Á®çÂêéÈáçËØï',
      [AgentErrorType.CHAT_RATE_LIMIT]: 'ËØ∑Ê±ÇËøá‰∫éÈ¢ëÁπÅÔºåËØ∑Á®çÂêéÈáçËØï',
      [AgentErrorType.CHAT_MODEL_UNAVAILABLE]: 'ÂØπËØùÊ®°ÂûãÊöÇÊó∂‰∏çÂèØÁî®',
      [AgentErrorType.AGENT_COMMUNICATION_ERROR]: 'Êô∫ËÉΩ‰ΩìÈÄö‰ø°ÈîôËØØ',
      [AgentErrorType.RESOURCE_EXHAUSTED]: 'Á≥ªÁªüËµÑÊ∫ê‰∏çË∂≥ÔºåËØ∑Á®çÂêéÈáçËØï',
      [AgentErrorType.SERVICE_UNAVAILABLE]: 'ÊúçÂä°ÊöÇÊó∂‰∏çÂèØÁî®ÔºåËØ∑Á®çÂêéÈáçËØï',
      [AgentErrorType.AUTHENTICATION_ERROR]: 'Ë∫´‰ªΩÈ™åËØÅÂ§±Ë¥•'
    };
    return messageMap[error.type] || error.message;
  }

  private static isRetryableError(type: AgentErrorType): boolean {
    const retryableErrors = [
      AgentErrorType.CAD_ANALYSIS_TIMEOUT,
      AgentErrorType.POSTER_GENERATION_FAILED,
      AgentErrorType.POSTER_TIMEOUT,
      AgentErrorType.CHAT_API_ERROR,
      AgentErrorType.CHAT_RATE_LIMIT,
      AgentErrorType.CHAT_MODEL_UNAVAILABLE,
      AgentErrorType.AGENT_COMMUNICATION_ERROR,
      AgentErrorType.RESOURCE_EXHAUSTED,
      AgentErrorType.SERVICE_UNAVAILABLE
    ];
    return retryableErrors.includes(type);
  }
}

// ÂÖ®Â±ÄÈîôËØØÂ§ÑÁêÜÂô®
export class GlobalErrorHandler {
  private static instance: GlobalErrorHandler;
  private errorCount = 0;
  private lastErrorTime = 0;
  private circuitBreakerOpen = false;
  private readonly circuitBreakerTimeout = 60000; // 1ÂàÜÈíü
  private readonly timeWindowMs = 60000; // 1ÂàÜÈíüÁ™óÂè£
  private readonly errorThreshold = parseInt(process.env.ERROR_THRESHOLD || '50'); // ÈîôËØØÈòàÂÄº
  private circuitBreakerResetTimer?: NodeJS.Timeout;

  static getInstance(): GlobalErrorHandler {
    if (!GlobalErrorHandler.instance) {
      GlobalErrorHandler.instance = new GlobalErrorHandler();
    }
    return GlobalErrorHandler.instance;
  }

  /**
   * Â§ÑÁêÜAPIÈîôËØØ
   */
  async handleError(
    error: any,
    request: NextRequest,
    context?: any
  ): Promise<NextResponse> {
    const startTime = Date.now();
    
    try {
      // ÈîôËØØÂàÜÁ±ª
      const classification = ErrorClassifier.classifyError(error);
      
      // ÂàõÂª∫Ê†áÂáÜÂåñÈîôËØØÂØπË±°
      const agentError = error instanceof AgentError 
        ? error 
        : new AgentError(
            classification.type,
            error.message || 'Êú™Áü•ÈîôËØØ',
            classification.severity,
            context?.agentType || 'unknown',
            {
              originalError: error,
              url: request.url,
              method: request.method,
              userAgent: request.headers.get('user-agent'),
              ...context
            }
          );

      // ËÆ∞ÂΩïÂà∞ÁõëÊéßÁ≥ªÁªü
      this.recordError(agentError);
      
      // Ê£ÄÊü•Êó∂Èó¥Á™óÂè£Âπ∂Êõ¥Êñ∞ÈîôËØØÁªüËÆ°
      this.checkTimeWindowAndUpdateStats();
      
      // Ê£ÄÊü•ÊòØÂê¶ÈúÄË¶ÅËß¶ÂèëÁÜîÊñ≠Âô®
      if (this.errorCount >= this.errorThreshold) {
        this.openCircuitBreaker();
      }
      
      // ËØ¶ÁªÜÊó•ÂøóËÆ∞ÂΩï
      ApiLogger.error('Global error handled', {
        errorId: agentError.id,
        type: agentError.type,
        message: agentError.message,
        statusCode: classification.statusCode,
        stack: error.stack,
        context: agentError.context,
        timestamp: agentError.timestamp
      });
      
      return this.createErrorResponse(agentError);

    } catch (handlerError) {
      // ÈîôËØØÂ§ÑÁêÜÂô®Êú¨Ë∫´Âá∫ÈîôÁöÑÂÖúÂ∫ïÂ§ÑÁêÜ
      console.error('Global error handler failed:', handlerError);
      
      return NextResponse.json(
        {
          success: false,
          error: {
            code: ErrorCode.INTERNAL_SERVER_ERROR,
            message: 'Á≥ªÁªüÂÜÖÈÉ®ÈîôËØØ',
            details: null
          },
          timestamp: new Date().toISOString()
        },
        { status: 500 }
      );
    }
  }

  /**
   * ÂàõÂª∫ÈîôËØØÂìçÂ∫î
   */
  private createErrorResponse(
    classification: any,
    error: AgentError,
    request: NextRequest
  ): NextResponse {
    const errorCode = this.mapErrorTypeToCode(classification.type);
    
    // ÂºÄÂèëÁéØÂ¢ÉËøîÂõûËØ¶ÁªÜÈîôËØØ‰ø°ÊÅØ
    const isDevelopment = process.env.NODE_ENV === 'development';
    
    const errorResponse = {
      success: false,
      error: {
        code: errorCode,
        message: classification.userMessage,
        type: classification.type,
        severity: classification.severity,
        retryable: classification.shouldRetry,
        details: isDevelopment ? {
          originalMessage: error.message,
          stack: error.stack,
          context: error.context
        } : null
      },
      timestamp: new Date().toISOString(),
      requestId: error.context?.requestId,
      ...(classification.shouldRetry && {
        retryAfter: this.calculateRetryDelay(classification.type)
      })
    };

    return NextResponse.json(errorResponse, { 
      status: classification.statusCode,
      headers: {
        'X-Error-Type': classification.type,
        'X-Error-Severity': classification.severity,
        ...(classification.shouldRetry && {
          'Retry-After': String(this.calculateRetryDelay(classification.type) / 1000)
        })
      }
    });
  }

  /**
   * Êò†Â∞ÑÈîôËØØÁ±ªÂûãÂà∞ÈîôËØØ‰ª£Á†Å
   */
  private mapErrorTypeToCode(type: AgentErrorType): ErrorCode {
    const codeMap: Record<AgentErrorType, ErrorCode> = {
      [AgentErrorType.CAD_FILE_PARSE_ERROR]: ErrorCode.VALIDATION_ERROR,
      [AgentErrorType.CAD_FORMAT_UNSUPPORTED]: ErrorCode.VALIDATION_ERROR,
      [AgentErrorType.CAD_FILE_CORRUPTED]: ErrorCode.VALIDATION_ERROR,
      [AgentErrorType.CAD_ANALYSIS_TIMEOUT]: ErrorCode.TIMEOUT_ERROR,
      [AgentErrorType.POSTER_GENERATION_FAILED]: ErrorCode.INTERNAL_SERVER_ERROR,
      [AgentErrorType.POSTER_TEMPLATE_ERROR]: ErrorCode.VALIDATION_ERROR,
      [AgentErrorType.POSTER_RESOURCE_LIMIT]: ErrorCode.RATE_LIMIT_ERROR,
      [AgentErrorType.POSTER_TIMEOUT]: ErrorCode.TIMEOUT_ERROR,
      [AgentErrorType.CHAT_CONTEXT_LOST]: ErrorCode.VALIDATION_ERROR,
      [AgentErrorType.CHAT_API_ERROR]: ErrorCode.EXTERNAL_SERVICE_ERROR,
      [AgentErrorType.CHAT_RATE_LIMIT]: ErrorCode.RATE_LIMIT_ERROR,
      [AgentErrorType.CHAT_MODEL_UNAVAILABLE]: ErrorCode.SERVICE_UNAVAILABLE,
      [AgentErrorType.AGENT_COMMUNICATION_ERROR]: ErrorCode.EXTERNAL_SERVICE_ERROR,
      [AgentErrorType.RESOURCE_EXHAUSTED]: ErrorCode.SERVICE_UNAVAILABLE,
      [AgentErrorType.SERVICE_UNAVAILABLE]: ErrorCode.SERVICE_UNAVAILABLE,
      [AgentErrorType.AUTHENTICATION_ERROR]: ErrorCode.AUTHENTICATION_ERROR
    };
    return codeMap[type] || ErrorCode.INTERNAL_SERVER_ERROR;
  }

  /**
   * ËÆ°ÁÆóÈáçËØïÂª∂Ëøü
   */
  private calculateRetryDelay(type: AgentErrorType): number {
    const delayMap: Record<AgentErrorType, number> = {
      [AgentErrorType.CHAT_RATE_LIMIT]: 60000, // 1ÂàÜÈíü
      [AgentErrorType.POSTER_RESOURCE_LIMIT]: 30000, // 30Áßí
      [AgentErrorType.RESOURCE_EXHAUSTED]: 120000, // 2ÂàÜÈíü
      [AgentErrorType.SERVICE_UNAVAILABLE]: 60000, // 1ÂàÜÈíü
    };
    return delayMap[type] || 5000; // ÈªòËÆ§5Áßí
  }

  /**
   * Êõ¥Êñ∞ÈîôËØØÁªüËÆ°
   */
  private updateErrorStats(): void {
    // ‰ΩøÁî®ÂéüÂ≠êÊìç‰ΩúÈÅøÂÖçÂπ∂ÂèëÈóÆÈ¢ò
    const now = Date.now();
    this.errorCount++;
    this.lastErrorTime = now;
  }

  /**
   * Ê£ÄÊü•Êó∂Èó¥Á™óÂè£Âπ∂Êõ¥Êñ∞ÁªüËÆ°
   */
  private checkTimeWindowAndUpdateStats(): void {
    const now = Date.now();
    
    // Ê£ÄÊü•Êó∂Èó¥Á™óÂè£ÊòØÂê¶ËøáÊúü
    if (this.lastErrorTime > 0 && now - this.lastErrorTime > this.timeWindowMs) {
      this.errorCount = 0;
    }
    
    // Êõ¥Êñ∞ÁªüËÆ°
    this.errorCount++;
    this.lastErrorTime = now;
  }

  /**
   * Ê£ÄÊü•ÊòØÂê¶Â∫îËØ•Ëß¶ÂèëÁÜîÊñ≠Âô®
   */
  private shouldTriggerCircuitBreaker(): boolean {
    const now = Date.now();
    
    // ÈáçÁΩÆËÆ°Êï∞Âô®ÔºàÂ¶ÇÊûúË∂ÖËøáÊó∂Èó¥Á™óÂè£Ôºâ
    if (now - this.lastErrorTime > this.timeWindowMs) {
      this.errorCount = 0;
      this.lastErrorTime = now; // ‰øÆÂ§çÔºöÊõ¥Êñ∞Êó∂Èó¥Êà≥
    }
    
    return this.errorCount >= this.errorThreshold;
  }

  /**
   * ÂºÄÂêØÁÜîÊñ≠Âô®
   */
  private openCircuitBreaker(): void {
    if (this.circuitBreakerOpen) {
      return; // ÈÅøÂÖçÈáçÂ§çÂºÄÂêØ
    }
    
    this.circuitBreakerOpen = true;
    console.warn('üö® Circuit breaker opened due to high error rate');
    
    // Ê∏ÖÈô§‰πãÂâçÁöÑÂÆöÊó∂Âô®
    if (this.circuitBreakerResetTimer) {
      clearTimeout(this.circuitBreakerResetTimer);
    }
    
    // ËÆæÁΩÆËá™Âä®ÊÅ¢Â§ç
    this.circuitBreakerResetTimer = setTimeout(() => {
      this.circuitBreakerOpen = false;
      this.errorCount = 0;
      this.lastErrorTime = 0;
      console.info('‚úÖ Circuit breaker closed, service restored');
    }, this.circuitBreakerTimeout);
  }

  /**
   * ËÆ∞ÂΩïÈîôËØØÂà∞ÁõëÊéßÁ≥ªÁªü
   */
  private recordError(error: AgentError): void {
    // ËøôÈáåÂèØ‰ª•ÈõÜÊàêÂ§ñÈÉ®ÁõëÊéßÁ≥ªÁªüÔºåÂ¶Ç Sentry, DataDog Á≠â
    // ÁõÆÂâç‰ΩøÁî®ÊéßÂà∂Âè∞ËæìÂá∫‰Ωú‰∏∫Âç†‰ΩçÁ¨¶
    console.error('Error recorded:', {
      id: error.id,
      type: error.type,
      message: error.message,
      timestamp: error.timestamp
    });
  }

  /**
   * ÂàõÂª∫ÈîôËØØÂìçÂ∫î
   */
  private createErrorResponse(error: AgentError): NextResponse {
    const responseBody = {
      success: false,
      error: {
        code: this.getErrorCode(error.type),
        message: error.userMessage,
        type: error.type,
        ...(process.env.NODE_ENV === 'development' && {
          details: error.message,
          stack: error.stack
        })
      },
      requestId: error.id,
      timestamp: error.timestamp,
      ...(error.retryable && {
        retryAfter: this.getRetryDelay(error.type)
      })
    };

    return NextResponse.json(responseBody, {
      status: error.statusCode,
      headers: {
        'Content-Type': 'application/json',
        'X-Request-ID': error.id
      }
    });
  }

  /**
   * Ê£ÄÊü•ÁÜîÊñ≠Âô®Áä∂ÊÄÅ
   */
  isCircuitBreakerOpen(): boolean {
    return this.circuitBreakerOpen;
  }
  
  /**
   * Ëé∑ÂèñÈîôËØØÁªüËÆ°
   */
  getErrorStats() {
    return {
      errorCount: this.errorCount,
      lastErrorTime: this.lastErrorTime,
      circuitBreakerOpen: this.circuitBreakerOpen,
      errorThreshold: this.errorThreshold,
      timeWindowMs: this.timeWindowMs
    };
  }
}

// ‰∏≠Èó¥‰ª∂ÂåÖË£ÖÂô®
export function withGlobalErrorHandler(
  handler: (req: NextRequest, context?: any) => Promise<NextResponse>
) {
  return async (req: NextRequest, context?: any): Promise<NextResponse> => {
    const errorHandler = GlobalErrorHandler.getInstance();
    
    // Ê£ÄÊü•ÁÜîÊñ≠Âô®Áä∂ÊÄÅ
    if (errorHandler.isCircuitBreakerOpen()) {
      const requestId = randomUUID();
      return NextResponse.json(
        {
          success: false,
          error: {
            code: ErrorCode.SERVICE_UNAVAILABLE,
            message: 'ÊúçÂä°ÊöÇÊó∂‰∏çÂèØÁî®ÔºåËØ∑Á®çÂêéÈáçËØï',
            type: AgentErrorType.SERVICE_UNAVAILABLE
          },
          requestId,
          timestamp: new Date().toISOString(),
          retryAfter: 60
        },
        { 
          status: 503,
          headers: {
            'Content-Type': 'application/json',
            'X-Request-ID': requestId,
            'Retry-After': '60'
          }
        }
      );
    }

    try {
      return await handler(req, context);
    } catch (error) {
      return await errorHandler.handleError(error, req, context);
    }
  };
}

// ÂØºÂá∫Âçï‰æãÂÆû‰æã
export const globalErrorHandler = GlobalErrorHandler.getInstance();